import AppView from '../views/AppView';
import AppModel from '../models/AppModel';

export default class App {
  constructor(state) {
    this.state = state;
    this.view = new AppView();
  }

  async start() {
    const input = document.querySelector('.input');
    input.addEventListener('keyup', function getQuery() {
      this.state = {
        url: `https://www.googleapis.com/youtube/v3/search?key=AIzaSyAmAA1upineqbIP2EW1Jd1s4RCgFYj2jM8&type=video&part=snippet&maxResults=15&q=${input.value}`,
      };
    });
    const model = new AppModel(this.state);
    const data = await model.getClipNames();

    const view = new AppView(data);

    view.render();
  }
}
